<template>
    <div class="container">
        <header class="header"></header>
         <div class="content">
             <van-field v-model="mobile"   placeholder=""   :error-message="usertel"  clearable />
             <van-field v-model="password" type="password" placeholder="" :error-message="pass"   clearable     />
              <van-button type="primary" :loading="loading" loading-text="..." size="large" :disabled="zhud" @click="login"></van-button>
              <van-divider @click="toRegister"></van-divider>
         </div>
    </div>
</template>
<script>

import axios from 'axios'

export default {
    data () {
        return {
            // tel:'',
            // password:'',
            mobile:'',
            password:'',
            zhud:false,
            loading:false,

        }
    },
    computed: {
        usertel () {
            if (this.mobile === ""){
                return ''
            }else if(!/^[1][3,4,5,7,8][0-9]{9}$/.test(this.mobile)){
                return ''
            }else {
                return ''
            }
        },
        pass () {
            if (this.password === ""){
                return ''
            }else if(this.password.length<6){
                return '6'
            }else {
                return ''
            }
        }
    },

    created(){
        this.reallR()
    },
    methods: {
        toRegister () {
            this.$router.replace('/register')
        },
        login () {
            if (this.tel === '' || this.usertel === '') {
                this.$toast('')
                return
            }
            if (this.password === '' || this.pass === '6') {
                this.$toast('')
                return
            }
            // if (this.sms === '' || this.sms !== this.adminCode) {
            //     this.$toast('')
            //     return
            // }
            this.reallR()
        },
        reallR () {

            this.zhud=true
            this.loading=true
            var $this=this;
            var param={
              mobile:this.mobile,
              password:this.password,
            }
            // let url = this.api.userApi.getMologin
            let url = "/login/user/getTest"
            // let url = this.api.userApi.getMologin
            this.axios.post(url,param).then((res) => {

              console.log(res);

            this.zhud=false
            this.loading=false
            if(res.status ===200){

              this.$toast('')
              localStorage.setItem('isLogin','ok') //
              // this.$router.back()  //
              // this.$router.replace('/index')
            }
            else {
                  this.$toast('')
            }
            // if (res.success === 2) {
            //     this.$toast('')
            // } else if(res.data === -1){
            //     this.$toast('')
            // }else if (res.data === 0) {
            //     this.$toast('')
            // } else {
            // this.$toast('')
            // localStorage.setItem('isLogin','ok') //
            // this.$router.back()  //
            // }
        })
        }
    }
}
</script>



