<template>
    <div class="container">
        <header class="header"></header>
         <div class="content">
             <van-field
                 v-model="tel"
                 placeholder=""
                 :error-message="usertel"

                  clearable
             />
             <van-field
                 v-model="password"
                 type="password"
                 placeholder=""
                 :error-message="pass"

                 clearable
             />
             <van-field
                 v-model="sms"
                 center
                 clearable
                 placeholder=""
                 :error-message="test"
              >
             <van-button :disabled="flag" slot="button" size="small" type="primary" @click="sendCode">{{ buttonmsg }}</van-button>

             </van-field>
              <van-button type="primary" :loading="loading" loading-text="..." size="large" :disabled="zhud" @click="register"></van-button>
              <van-divider @click="toLogin"></van-divider>
         </div>
    </div>
</template>
<script>

import axios from 'axios'


export default {
    data () {
        return {
            tel:'',
            password:'',
            sms:'',
            company_user_id:'',
            adminCode:'',
            buttonmsg:'',
            flag:false,
            zhud:false,
            loading:false
        }
    },
    computed: {
        usertel () {
            if (this.tel === ""){
                return ''
            }else if(!/^[1][3,4,5,7,8][0-9]{9}$/.test(this.tel)){
                return ''
            }else {
                return ''
            }
        },
        pass () {
            if (this.password === ""){
                return ''
            }else if(this.password.length<6){
                return '6'
            }else {
                return ''
            }
        },
        test () {
            if (this.sms === ""){
                return ''
            }else if(this.sms.length !== 6){
                return ''
            }else {
                return ''
            }
        }
    },
    methods: {
        toLogin () {
           this.$router.push({
               name:'login'
             })
        },
        sendCode () {
            let time = 20
            let timer
            timer = setInterval(()=>{
                time --
                if(time === 0){
                    clearInterval(timer)
                    this.flag=false
                    this.buttonmsg = ''
                    return
                }
                this.flag = true
                this.buttonmsg = time + ''
            },1000)
            this.getCode()
        },
        getCode () {
            if(!/^[1][3,4,5,7,8][0-9]{9}$/.test(this.tel) || this.tel===""){
                this.$toast('')
            }else{
                axios.get('/user/mobileRegister'+this.tel).then(res=>{
                  console.log(res);
                    // if(res.data.code === 5){
                    //     this.$toast('')
                    // }else if(res.data === 3){
                    //     this.$toast('')
                    // }else{
                    //     this.adminCode = res.data.code
                    //     console.log(this.adminCode)
                    // }
                })
            }
        },
        register () {
            if (this.tel === '' || this.usertel === '') {
                this.$toast('')
                return
            }
            if (this.password === '' || this.pass === '6') {
                this.$toast('')
                return
            }
            // if (this.sms === '' || this.sms !== this.adminCode) {
            //     this.$toast('')
            //     return
            // }
            this.reallR()
        },
        reallR () {
          var $this=this;
          this.zhud=true
          this.loading=true

          let param = {
            mobile: this.tel,
            code: this.sms,
            password:this.password,
            company_user_id:this.company_user_id,
          }
          let url = "/login/user/mobileRegister"
          this.axios.post(url,param).then((res) => {
           console.log(res);
            this.zhud=false
            this.loading=false


             if (res.data.code ===1) {
                this.$toast('')
            } else {
            this.$toast('')
            }
        })
        }
    }
}
</script>



